\n    border: 1px solid #d3756b;\n    border-radius: 10px;\n}\n\n\n/*\n * footer\n */\nfooter {\n  color: #000000;\n  background-color: #d3756b;\n  text-align: center;\n  padding: 1rem 0 3rem;\n  margin-top: 3rem;\n}\n\nfooter .socials {\n  padding: 1rem 0;\n}\n\nfooter .socials a {\n  color: #000000;\n  /* margin: 1rem; */\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px;\n}\n\nfooter .socials a:hover,\nfooter .links a:hover {\n  color: #a75d5d;\n}\n\nfooter .links {\n  margin-bottom: 1.4rem;\n}\n\nfooter .links a {\n  color: #000000;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px 10px;\n}\n\nfooter .credit {\n  font-size: 0.8rem;\n}\n\nfooter .credit a {\n  color: #000000;\n  font-weight: 700;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px 0;\n}\n\nmain:focus,\nh1:focus,\nh2:focus,\na:focus,\narticle:focus,\np:focus,\nimg:focus,\ndiv[tabindex=\"0\"]:focus,\nbutton:focus {\n  border: 2px solid #1a1a1a;\n  cursor: pointer;\n}\n\n.skip-link {\n  position: absolute;\n  top: -54px;\n  left: 0;\n  /* padding: 8px; */\n  background-color: #a75d5d;\n  color: white;\n  z-index: 10000;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px 0;\n  font-size: 10px;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n", "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;EACT,aAAa;EACb,YAAY;EACZ,sBAAsB;EACtB,qBAAqB;AACvB;AACA;EACE,uBAAuB;AACzB;;AAEA;;EAEE,SAAS;EACT,UAAU;EACV,WAAW;EACX,iBAAiB;AACnB;;AAEA;EACE,uCAAuC;EACvC,kCAAkC;EAClC,yBAAyB;EACzB,eAAe;EACf,cAAc;EACd,aAAa;EACb,sBAAsB;AACxB;;AAEA;;;EAGE;AACF;;;;GAIG;;AAEH;;;;;GAKG;AACH;;;;;;GAMG;AACH;;;;;;GAMG;;AAEH;;EAEE;;AAEF;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,gBAAgB;EAChB,0CAA0C;EAC1C,gCAAgC;EAChC,eAAe;EACf,MAAM;EACN,OAAO;EACP,QAAQ;EACR,aAAa;AACf;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,cAAc;AAChB;;;;AAIA;EACE,cAAc;EACd,qBAAqB;EACrB,iBAAiB;EACj