Ae;EACf,gBAAgB;EAChB,aAAa;AACf;;AAEA;;EAEE,cAAc;AAChB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,cAAc;EACd,eAAe;EACf,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,eAAe;AACjB;;AAEA;;;;;;;;;EASE,yBAAyB;EACzB,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,cAAc;EACd,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,MAAM;AACR","sourcesContent":["* {\n  padding: 0;\n  margin: 0;\n  outline: none;\n  border: none;\n  box-sizing: border-box;\n  text-decoration: none;\n}\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  min-height: 100vh;\n}\n\nbody {\n  /* font-family: \"Roboto\", sans-serif; */\n  font-family: \"Poppins\", sans-serif;\n  background-color: #f4f9f9;\n  font-size: 12px;\n  color: #424242;\n  display: flex;\n  flex-direction: column;\n}\n\n/*\n * Header\n *\n */\n/* \n.header {\n  min-height: 56px;\n  transition: min-height 0.3s;\n} */\n\n/* .header__inner {\n  width: 100%;\n  text-align: center;\n  margin-left: auto;\n  margin-right: auto;\n} */\n/* .header__title {\n  font-weight: 300;\n  font-size: 4em;\n  margin: 0.5em 0.25em;\n  display: inline-block;\n  color: #212121;\n} */\n/* \n.header__menu {\n  font-size: 32px;\n  margin: 10px auto;\n  display: block;\n  width: 30px;\n} */\n\n/*\n * Top level navigation\n */\n\n.navbar {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 20px 7%;\n  background-color: rgba(244, 249, 249, 0.8);\n  border-bottom: 1px solid #513c28;\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 9999;\n}\n\n.navbar .navbar-logo {\n  display: flex;\n  justify-content: center;\n  align-item: center;\n  line-height: 44px;\n  font-size: 2rem;\n  font-weight: 700;\n  color: #47474f;\n  font-style: italic;\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.navbar .navbar-logo span {\n  color: #d3756b;\n}\n\n\n\n.navbar .navbar-nav a {\n  color: #47474f;\n  display: inline-block;\n  font-size: 1.3rem;\n  margin: 0 1rem;\n}\n.navbar .navbar-nav a:hover {\n  color: #d3756b;\n}\n\n.navbar .navbar-nav a::after {\n  content: \"\";\n  display: block;\n  padding-bottom: 0.5rem;\n  border-bottom: 0.1rem solid #d3756b;\n  transform: scaleX(0);\n  transition: 0.2s linear;\n}\n\n.navbar .navbar-nav a:hover::after {\n  transform: scaleX(0.5);\n}\n\n.navbar .navbar-extra a {\n  color: #47474f;\n  margin: 0 1rem;\n  min-height: 44px;\n  min-width: 44px;\n}\n.navbar .navbar-extra a:hover {\n  color: #d3756b;\n}\n\n#hamburgerMenu {\n  font-size: 24px;\n  margin: auto;\n  justify-content: center;\n  align-item: center;\n  display: block;\n  background: none;\n  min-height: 44px;\n  min-width: 44px;\n  \n}\n\n/*\n * Jumbotron\n */\n\n.hero {\n  margin-top: 5.8rem;\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  /* width: 100%; */\n  text-align: center;\n  background-image: url(\"../public/images/heros/hero-image_2-bg.jpg\");\n  background-position: center;\n  background-color: green;\n  background-repeat: no-repeat;\n  background-size: cover;\n\n  position: relative;\n}\n\n.hero__inner {\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.hero__title {\n  color: black;\n  font-weight: 500;\n  font-size: 36px;\n  text-shadow: 1px 1px 13px rgba(255, 255, 255, 0.8);\n}\n\n.hero__tagline {\n  color: black;\n  text-shadow: 1px 1px 13px rgba(255, 255, 255, 0.8);\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 300;\n}\n\n/*\n * main\n */\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n  padding: 32px;\n  flex: 1;\n}\n\n/*\n * content\n */\n\n.content {\n  /* padding: 32px; */\n  margin: 0 auto;\n  min-height: 100%;\n}\n.content .content__heading {\n  margin: 0 auto;\n  font-weight: normal;\n  font-size: 2rem;\n  text-align: center;\n}\n\n.restaurant-item__not__found {\n  margin: 0 auto;\n  font-weight: normal;\n  font-size: 1rem;\n}\n/*\n * headline\n */\n\n.headline__figure {\n  width: 100%;\n}\n\n.headline__figure img {\n  width: 100%;\n}\n\n.headline__figure figcaption {\n  text-align: center;\n  color: #666666;\n  font-size: 13px;\n  font-weight: 400;\n  margin-top: 8px;\n}\n\n.headline__content {\n  width: 100%;\n  padding: 16px 0;\n}\n\n.headline__title {\n  font-size: 24px;\n  font-weight: 500;\n}\n\n.headline__description {\n  font-size: 12px;\n  margin-top: 12px;\n}\n\n.headline__button {\n  font-family: \"Roboto\", sans-serif;\n  text-transform: uppercase;\n  margin-top: 24px;\n  cursor: pointer;\n  padding: 8px 16px;\n  color: white;\n  border: none;\n  border-radius: 5px;\n  background-color: #2c3e50;\n  transition: opacity 0.3s;\n}\n\n.headline__button:hover {\n  opacity: 0.8;\n}\n\n/*\n * latest\n */\n\n.latest {\n  width: 100%;\n  margin: 60px auto;\n  text-align: center;\n}\n\n.latest__label {\n  font-size: 32px;\n  font-weight: lighter;\n}\n\n.latest__label::after {\n  content: \"\";\n  margin-top: 16px;\n  display: block;\n  border-bottom: 1px solid #eeeeee;\n}\n\n/*\n * post\n */\n\n.posts {\n  margin: 32px auto auto;\n  text-align: left;\n}\n\n/*\n * post item\n */\n\n.post-item {\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.post-item__content {\n  padding: 16px;\n}\n\n.post-item__thumbnail {\n  width: 100%;\n}\n\n.post-item__date {\n  font-size: 10px;\n  text-transform: uppercase;\n  color: #999;\n}\n\n.post-item__date__author {\n  color: red;\n  text-decoration: none;\n}\n\n.post-item__title {\n  font-weight: 500;\n  font-size: 16px;\n  margin-top: 16px;\n  transition: 0.3s opacity;\n}\n\n.post-item__title:hover {\n  opacity: 0.5;\n}\n\n.post-item__title a {\n  text-decoration: none;\n  color: inherit;\n}\n\n.post-item__description {\n  margin-top: 16px;\n  font-size: 12px;\n  line-height: 1.5em;\n}\n\n/*\n  Restaurants\n*/\n \n.restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n  margin: 32px 0;\n}\n \n \n/*\n  Restaurant Item\n*/\n \n.restaurant-item {\n  width: 100%;\n  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */\n  box-shadow: 0 1px 2px 2px rgba(0, 0, 0, 0.4666666667);\n  border-radius: 8px;\n  overflow: hidden;\n  /* transition: 0.25s ease-in-out; */\n  transition: transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out;\n}\n \n.restaurant-item:hover {\n  transform: scale(1.01);\n  box-shadow: 0 4px 6px 6px rgba(0, 0, 0, 0.2);\n}\n\n.restaurant-item__header {\n  position: relative;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\n  width: 100%;\n  transition: 0.3s ease-in-out;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: #d3756b;\n  color: #000000;\n}\n \n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\n  margin-left: 10px;\n  color: #000000;\n}\n \n.restaurant-item .restaurant-item__content {\n  padding: 16px;\n}\n \n.restaurant-item .restaurant-item__content h3 {\n  margin: 0 0 10px 0;\n}\n \n.restaurant-item .restaurant-item__content .name {\n  padding: 15px 15px 15px 0;\n  color: #db0000;\n  text-decoration: none;\n  max-width: 64px;\n  max-height: 44px;\n}\n \n.restaurant-item .restaurant-item__content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4; /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n \n \n/*\n  Restaurant\n*/\n \n.restaurant {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 800px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 18px 16px;\n}\n.restaurant .restaurant__name {\n  display: flex;\n  justify-content: center;\n  align-item: center;\n  font-size: 2rem;\n  margin-bottom: 16px;\n}\n.restaurant .restaurant__poster {\n  width: 100%;\n  border-radius: 10px;\n}\n \n.restaurant .restaurant__info h4 {\n  margin: 8px 0;\n}\n.restaurant 